import config from '../../components/config/config.vue'
import selectAddress from '@/components/yixuan-selectAddress/yixuan-selectAddress.vue'
export default{
	components: {selectAddress},
	data(){
		return{
			cid:0,//商品id
			
			sname:"",
			sphone:"",
			saddress:"",
			sdetail:"",
			rname:"",
			rphone:"",
			raddress:"",
			rdetail:"",
			
			//物品信息
			selecttype:['日用品', '食品', '文件','数码产品','衣物','其他'],
			thingtype:"",
			thingweight:"",
			thingmess:"",
			
		}
	},
	onLoad:function (option)  {			
		this.cid=parseInt(option.id	)	 
	},
	onShow() {				
		uni.startPullDownRefresh();				
	},
	onTabItemTap(){
		uni.startPullDownRefresh();
	},
	onPullDownRefresh() {
		this.token = uni.getStorageSync('token');
		
	    setTimeout(function () {
	        uni.stopPullDownRefresh();
	    }, 1000);
	},
	methods:{
		btnClick() {
		        this.$refs.selectAddress.show()
		    },
		successSelectAddress(address){ //选择成功回调
		   this.saddress=address
		},
		btnClick2() {
		        this.$refs.selectAddress2.show()
		},
		successSelectAddress2(address){ //选择成功回调
		   this.raddress=address
		},
		thingtypeclick(){
			var that=this
			uni.showActionSheet({
			    itemList:that.selecttype,
			    success: function (res) {
					that.thingtype=that.selecttype[res.tapIndex]
			    },
			    fail: function (res) {
					//不进行任何操作
			        // console.log(res.errMsg);
			    }
			});		
		},
		send(){
			// 表单验证
			var err = {
			    msg: '',
			    ok: true
			}
			if(this.sname.length<=0 || this.sname.length>10 || this.rname.length<=0 || this.rname.length>10){
				err.ok = false;
				err.msg = "姓名长度限制1-10"
			}
			
			if(!(/^1[3456789]\d{9}$/.test(this.sphone)) || !(/^1[3456789]\d{9}$/.test(this.rphone))){
				err.ok = false;
				err.msg = "手机号码有误"
			}
			
			if(this.saddress.length<=0 || this.raddress.length<=0 ){
				err.ok = false;
				err.msg = "地址不能为空"
			}
			
			if(this.sdetail.length>200 || this.rdetail.length>200 ){
				err.ok = false;
				err.msg = "地址详细长度限制1000"
			}
			
			if(this.thingtype.length<=0){
				err.ok = false;
				err.msg = "货物类型不能为空"
			}
			
			if(this.thingweight.length<=0){
				err.ok = false;
				err.msg = "货物重量不能为空"
			}
			
			if(this.thingmess.length>1000){
				err.ok = false;
				err.msg = "备注限制1000"
			}				
			
			if (!err.ok) {
			    uni.showModal({
			        title: '提示',
			        content: err.msg
			    })
			    return;
			}
			
			uni.showLoading({
			    title: '数据处理中'
			});
			var value = uni.getStorageSync('token')
			if(value){
			  	uni.request({
			  	    url: config.httpUrl +'/api/v1/commodity/order',
			  		method:"POST",
			  	    header: {
			  		   'Authorization' : value,
			  	    },
					data:{
						'cid':this.cid,//商品id							
						'sname':this.sname,
						'sphone':this.sphone,
						'saddress':this.saddress,
						'sdetail':this.sdetail,
						'rname':this.rname,
						'rphone':this.rphone,
						'raddress':this.raddress,
						'rdetail':this.rdetail,
						'thingtype':this.thingtype,
						'thingweight':parseInt(this.thingweight),
						'thingmess':this.thingmess,
					},
			  	    success: (res) => {
			  			var jsondata=res.data
			  			if(jsondata.code=='0'){
							config.txtshow('下单成功')
							var tmpoid=jsondata.data
							uni.request({
							    url: config.httpUrl +'/api/v1/commodity/transportlog',
								method:"POST",
							    header: {
								   'Authorization' : value,
							    },
								data:{
									'oid':tmpoid, //0代表所有id
									'content':'下单成功'
								},
							    success: (res) => {
									var jsondata=res.data
									if(jsondata.code=='0'){
										console.log('更新物流信息成功')
									}else{
										 config.txtshow(jsondata.msg)				  				
									}														
							    },
								fail: () =>{  
									 config.txtshow('网络异常')
							     }, 
							});
							
							uni.redirectTo({
								url:'user_dealing'
							})							
			  			}else{
			  				 config.txtshow(jsondata.msg)	
							console.log(jsondata)							 
			  			}														
			  	    },
			  		fail: () =>{  
			  			 config.txtshow('网络异常')
			  	     }, 
			  	});
			  }else{
			  	//跳转到登录界面
			  config.txtshow("未登录")	
			}
			
		}
		    											
	}
}
